/* updates to tile header height should be reflected in tileSize.ts */
.unselectable {
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}
.trimmedTextWithEllipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.preTextWithEllipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: pre;
}
.skittlesNormal {
  opacity: 0;
  border: 1px solid #A6A6A6;
  color: #333333;
  background-color: #FFF;
  margin-bottom: 5px;
  margin-left: 5px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
  -webkit-transition-delay: 0s;
  /* Safari */
  transition-delay: 0s;
}
.skittlesHovered {
  background-color: #F4F4F4;
}
.skittlesPressed {
  border-color: #777777;
  background-color: #EAEAEA;
}
.skittlesIconNormal {
  vertical-align: middle;
  text-align: center;
  color: #A6A6A6;
}
.skittlesIconHovered {
  color: #777777;
}
.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.5s linear, left 0.5s linear, transform 0.5s;
  -webkit-transition: opacity 0.5s linear, left 0.5s linear, transform 0.5s;
}
.hidden {
  visibility: hidden;
  /* Fully hide the element*/
  opacity: 0;
  transition: visibility 0s 0.5s, width 0s 0.5s, height 0s 0.5s, opacity 0.5s linear, left 0.5s linear, transform 0.5s;
  -webkit-transition: visibility 0s 0.5s, width 0s 0.5s, height 0s 0.5s, opacity 0.5s linear, left 0.5s linear, transform 0.5s;
}
.hiddenNoTransition {
  visibility: hidden;
  opacity: 0;
}
.no-border-radius {
  border-radius: 0;
}
.expAppBarMenu {
  position: absolute;
  left: 10px;
  top: 7px;
  content: '';
}
.expAppBar {
  position: absolute;
  left: 10px;
  content: '';
}
